import bcrypt from 'bcryptjs';
import jwt from 'jsonwebtoken';
import { User, AuthFormData, RegisterFormData } from '@/types/auth';
import { db, generateId } from './database';

const JWT_SECRET = process.env.JWT_SECRET || 'your-secret-key-change-in-production';

export async function hashPassword(password: string): Promise<string> {
  return bcrypt.hash(password, 12);
}

export async function verifyPassword(password: string, hashedPassword: string): Promise<boolean> {
  return bcrypt.compare(password, hashedPassword);
}

export function generateToken(user: User): string {
  return jwt.sign(
    { 
      userId: user.id, 
      email: user.email, 
      role: user.role 
    },
    JWT_SECRET,
    { expiresIn: '7d' }
  );
}

export function verifyToken(token: string): { userId: string; email: string; role: string } | null {
  try {
    return jwt.verify(token, JWT_SECRET) as { userId: string; email: string; role: string };
  } catch {
    return null;
  }
}

export async function registerUser(data: RegisterFormData): Promise<{ user: User; token: string } | { error: string }> {
  try {
    // Check if user already exists
    const existingUser = db.getUserByEmail(data.email);
    if (existingUser) {
      return { error: 'User with this email already exists' };
    }

    // Validate password match
    if (data.password !== data.confirmPassword) {
      return { error: 'Passwords do not match' };
    }

    // Hash password
    const hashedPassword = await hashPassword(data.password);

    // Create user
    const user: User = {
      id: generateId(),
      email: data.email,
      name: data.name,
      role: data.role,
      createdAt: new Date(),
      updatedAt: new Date(),
    };

    // Store user with hashed password (in real app, password would be in separate table)
    const userWithPassword = { ...user, password: hashedPassword };
    db.createUser(userWithPassword as any);

    // Generate token
    const token = generateToken(user);

    return { user, token };
  } catch (error) {
    return { error: 'Registration failed' };
  }
}

export async function loginUser(data: AuthFormData): Promise<{ user: User; token: string } | { error: string }> {
  try {
    // Find user
    const userWithPassword = db.getUserByEmail(data.email) as any;
    if (!userWithPassword) {
      return { error: 'Invalid email or password' };
    }

    // Verify password
    const isValidPassword = await verifyPassword(data.password, userWithPassword.password);
    if (!isValidPassword) {
      return { error: 'Invalid email or password' };
    }

    // Remove password from user object
    const { password, ...user } = userWithPassword;

    // Generate token
    const token = generateToken(user);

    return { user, token };
  } catch (error) {
    return { error: 'Login failed' };
  }
}

export function getCurrentUser(token: string): User | null {
  const decoded = verifyToken(token);
  if (!decoded) return null;

  const user = db.getUserById(decoded.userId);
  if (!user) return null;

  // Remove password if it exists
  const { password, ...userWithoutPassword } = user as any;
  return userWithoutPassword;
}